.Imageninfo{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
}

.BasicInfo{
    background: rgba(7, 182, 7, 0.7);
    color: purple;
    font-size: 19px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    padding: 2px 10px 2px 10px;
    /*height: 40%;*/
    /*width: 35%;*/
    border-radius: 10px;
}
.UploadFiles{
    margin-top: 10px;
}
#UploadHeading{
    font-size: 19px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.dp{
    position: relative;
    border-radius: 50%;
    height:250px;
    width: 250px;
    
}
#dpselectionform,#fileselectionform{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;

}
.ViewUploadedbtn{
    margin-top: 18px;
    height: 40px;
    width: 100%;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    color: rgb(14, 90, 119);
    border: 2px solid skyblue;
    font-weight: 600;
    text-align: center;
    padding: 5px;
    font-size: 18px;
    overflow: hidden;
    background: white;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
}
.ViewUploadedbtn:hover{
    background: rgb(14, 90, 119);
    color: white;
    -webkit-transform: scale(1.02);
        -ms-transform: scale(1.02);
            transform: scale(1.02);
}
.changedpbtn{
    margin-left: 35%;
    height: 26px;
    width: 70px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    border: 1px solid #00ad2d;
    color: white;
    background:#00ad2d;
}
#uploadrecbtn{
    height: 25px;
    width: 90px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    border: 1px solid #00ad2d;
    color: white;
    background:#00ad2d;
    margin-left: 5px;
}
.downloadUploadedFile{
    height: 25px;
    width: 100px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    border: 1px solid #00ad2d;
    color: white;
    background:#00ad2d;
    margin-left: 5px;
}
.uploadmessage,.FilesListOfUser{
    font-size: 16px;
    color: red;
}
.FilesListOfUser{
    text-align: center;
    color: black;
    padding-top: 5px;
    padding-bottom: 8px; 
    font-size: 18px;
    background: rgba(135, 206, 235,0.7);
}
#infotitle{
    color: brown;
}
#ChooseFile{
    padding:5px 5px;
    background:#00ad2d;
    border:1px solid #00ad2d;
    position:relative;
    color:#fff;
    border-radius:2px;
    font-size: 12px;
    cursor:pointer;
    width: 72px;
    height: 14px;
    margin-left: 5px;
}
.Choosedp{
    padding:5px 5px;
    background:#00ad2d;
    position:relative;
    color:#fff;
    font-size: 12px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    cursor:pointer;
    width: 70px;
    height: 16px;
}
.hide_file{
    position: absolute;
    z-index: 1000;
    opacity: 0;
    cursor: pointer;
    right: 0;
    top: 0;
    height: 100%;
    font-size: 24px;
    width: 100%;
}
.inputdp{
    background: orange;
    color: red;
    width: 190px;
}
.filedesc{
    width: 120px;
    height: 20px;
    border: #00ad2d;
}


