.hideaboutdisease,
.hideprevention,
.hidetreatement,
.hidetypeofdisease,
.hidesymptoms{
    display: none;
}
.showaboutdisease,
.showprevention,
.showtreatement,
.showtypeofdisease,
.showsymptoms{
    display: block;
}
.hideInfo{
    display: none;
}
.shoeInfo{
    display: block;
}
#DiseaseName{
    font-size: 25px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: 520;
}
#aboutdisease{
    font-size: 20px;
}
#SympDetails{
    font-size: 20px;
}
#Prevention{
    font-size: 20px;
}
#Treatment{
    font-size: 20px;
}
.explained{
    font-size: 18px;
    padding:0 8px;
}
.Disease{
    cursor: pointer;
}
#SympExplained{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap:wrap;
        flex-wrap:wrap;
    -ms-flex-pack: center;
        justify-content: center;
}



