.Confirmbutton,
.CancelForgotPassword{
    width: 93%;
    max-width: 820px;
    height: 45px;
    color: rgb(14, 90, 119);
    border: 2px solid skyblue;
    font-weight: 600;
    text-align: center;
    padding: 5px;
    overflow: hidden;
    margin-top: 12px;
    background: white;
}
.Confirmbutton:hover,.CancelForgotPassword:hover{
    background: skyblue;
    color: white;
}
.InputEmailForgotPassword{
    color: black;
    padding: 8px;
    width: 88%;
    text-align: center;
    max-width: 800px;
    height: 35px;
    margin-top: 12px;
    border: 2px solid blueviolet;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;  
}
.ForgotPassword{
    text-align: center;
    background: transparent;
}


