.gendertext,.genderStyling{
    font-size: 15px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    width: 300px;
    margin-top: 22px;
    text-align: center;
    -ms-flex-pack: center;
        justify-content: center;
    padding: 0 5px;
}
#appointmentformdetails,#AppointmentAddress{
    color: black;
    padding: 8px;
    text-align: center;
    width: 93%;
    max-width: 800px;
    height: 35px;
    margin-top: 12px;
    border: 2px solid skyblue;
}
.ContinueToBookAppointment,.CancelAppointment{
    width: 93%;
    max-width: 820px;
    height: 45px;
    color: rgb(14, 90, 119);
    border: 2px solid skyblue;
    font-weight: 600;
    text-align: center;
    padding: 5px;
    /*overflow: hidden;*/
    margin-top: 12px;
    background: white;

}
.ContinueToBookAppointment:hover,.CancelAppointment:hover{
    background: skyblue;
    color: white;
}
.DetailsForm{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: center;
        justify-content: center;
}


