.SearchSymptom{
    float: right;
    /*margin-top: 15px;*/
    padding: 5px;
    border: 1px solid black;
    /*color: black;*/
    width: 150px;
    /*background: transparent;*/
    /*border-radius: 8px;*/
    height: 40px;
    font-size: 15px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    margin-left: 10px;
    background: rgba(7, 182, 7, 0.7);
    color: white;
    border: 1px solid rgba(7, 182, 7, 0.7);
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
}
.InSymptomBookAppt{
    padding: 5px;
    width: auto;
    height: 40px;
    font-size: 15px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background: black;
    border-radius: 5px;
    color: white;
    margin-top: 5px;
    border: 1px solid rgba(7, 182, 7, 0.7);
}
.Symptomsearch_text{
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: 600;
    font-size: 20px;
}
.SearchSymptom:hover{
    -webkit-transform: scale(1.05);
        -ms-transform: scale(1.05);
            transform: scale(1.05);
}
.SymptomSelectnButton{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: center;
        justify-content: center;
}
