.ToContinueLoginButton,
.ForgotPasswordButton {
    width: 93%;
    max-width: 820px;
    height: 45px;
    color: rgb(14, 90, 119);
    border: 2px solid skyblue;
    font-weight: 600;
    text-align: center;
    padding: 5px;
    overflow: hidden;
    margin-top: 12px;
    background: white;
}
.ToContinueLoginButton:hover,.ForgotPasswordButton:hover{
    background: skyblue;
    color: white;
}
.InputFieldLogin,
.PasswordFieldLogin {
    color: black;
    padding: 8px;
    width: 88%;
    max-width: 800px;
    height: 35px;
    margin-top: 12px;
    border: 2px solid skyblue;
}
.LoginOuterDiv {
    text-align: center;
    background: transparent;
}


