.LogoName{
    font-family: 'Handycheera';
    font-size: 42px;
    z-index: 2;
    position: fixed;
    top: -37px;
    left: 2%;
    color: whitesmoke;
    /*background: transparent;*/
    /*box-shadow: 2px 3px 4px grey;*/
    width: 200px;
}
.LogoName:hover{
    color: rgb(7, 182, 7);
}
.NavbarMainClass{
    height: 69px;
    position: fixed;
    top: 0px;
    background-color: black;
    width: 100%;
    color: white;
    border-bottom: 2px solid rgb(7, 182, 7);
    padding-bottom: 5px;
    overflow: hidden;
    z-index: 2;  
}
.NavbarButton,.LogOutButton{
    width: 150px;
    height: 49px;
    margin-left:10px;
    margin-right: 10px;
    background: transparent;
    color: white;
    border-radius: 8px;
    border: 1px solid white;
    font-size: 15px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
}
.LogInButtonInsideApp{
    width: 160px;
    position: absolute;
    right: 15px;
    top: 7px;
}
.SignUpButtonInsideApp{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    position: absolute;
    top: 7px;
    right:180px;
}
.NavbarButton:hover,.LogOutButton:hover{
    color: white;
    background: rgb(7, 182, 7);
    border: 1px solid rgb(7, 182, 7);
    -webkit-transform: scale(1.05);
        -ms-transform: scale(1.05);
            transform: scale(1.05);bottom: 0px;
}
.NavbarHome{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    position: absolute;
    top: 7px;
    left: 300px;
}
.NavbarSick{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    position: absolute;
    top: 7px;
    left: 470px;
}
.NavbarBook{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    position: absolute;
    top: 7px;
    left: 640px;
}
.NavbarAbout{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    position: absolute;
    top: 7px;
    left: 810px;
}
.NavbarContact{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    position: absolute;
    top: 7px;
    left: 980px;
}
#fafaIcon{
    display: none;
}
.ForNavbarBackgorund{
    height: 60px;
    background: rgb(0,0,0,0.7);
    position: fixed;
    top: 0px;
    width: 100%;
    z-index: 1;
    border-bottom: 2px solid grey;
}

@media screen and (max-width: 1513px){

    .LogInButtonInsideApp{
        width: 160px;
        position: absolute;
        left: 45px;
        top: 430px;
    }
    .SignUpButtonInsideApp{
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
        position: absolute;
        top: 500px;
        left: 45px;
    }
    .ParticleBack{
        height: 100%;
    }
    #fafaIcon{
        display: block;
        position: absolute;
        right: 25px;
        top: 10px;
        color: whitesmoke;
        position: fixed;
        z-index: 6;
        cursor: pointer;
    }
    .NavbarMainClass{
        width: 250px;
        height: 600px;
        position: absolute;
        right: 5px;
        background-color: rgba(0, 0, 0, 0.7);
        -webkit-box-shadow: -2px 5px 5px grey;
                box-shadow: -2px 5px 5px grey;
        top: 8px;
        z-index: 5;
        position: fixed;
    }
    .NavbarHome{
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
        position: absolute;
        top: 80px;
        right: 25px;
        left: 45px;
    }
    .NavbarSick{
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
        position: absolute;
        top: 150px;
        left: 45px;
    }
    .NavbarBook{
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
        position: absolute;
        top: 220px;
        left: 45px;
    }
    .NavbarAbout{
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
        position: absolute;
        top: 290px;
        left: 45px;
    }
    .NavbarContact{
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
        position: absolute;
        top: 360px;
        left: 45px;
    }
    .LogoName{
        top: -38px;
    }
}


