.About{
    width: 100%;
    background: -webkit-gradient(linear,left top, left bottom,from(rgba(0, 208, 255,0.9)),to(#025047));
    background: -webkit-linear-gradient(rgba(0, 208, 255,0.9),#025047);
    background: -o-linear-gradient(rgba(0, 208, 255,0.9),#025047);
    background: linear-gradient(rgba(0, 208, 255,0.9),#025047);
    position: relative;
    top: 770px;
    height: 580px;
}
.AboutText{
    top: 20px;
    left: 45%;
    position: absolute;
    font-size: 30px;
    font-weight: 500;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.BoxContainerForImages1{
    position: relative;
    height: 520px;
    width: 704px;
    top:130px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: center;
        justify-content: center;
    margin: 0 auto;
}
.HemantDescription,
.AnkitDescription,
.RajanDescription,
.UnknownDescription{
    visibility: hidden;
    background: rgb(37, 82, 167);
}
.HemantDescription{
    position: absolute;
    top: 0px;
    height: 350px;
    width: 350px;
    font-size: 25px;
    font-family: Dosis,cursive;
}
.HemantPhoto{
    height: 370px;
    top: 40px;
    border-radius: 15px;
    /*border: 1px solid black;*/

}
.HemantPhoto:hover{
    visibility: visible;
    opacity: 0.5;
}

@media screen and (max-width: 720px){
    .BoxContainerForImages1{
        width: 100%;
        text-align: right;
   
    }/*
    .About{
        overflow-y: scroll;
        
    }*/
    .AboutText{
        text-align: center;
    }
    .HemantPhoto{
        height: 300px;
        width: 100%;
        /*border: 1px solid black;*/
    }
}



