.imgImage {
    height: 400px;
    width: 355px;
    border-bottom: 1px solid gray;
    -webkit-box-shadow: 1px 2px 5px gray;
            box-shadow: 1px 2px 5px gray;
    border-top-right-radius: 40px;
    border-top-left-radius: 40px;
}

.Image {
    -webkit-box-shadow: 2px 1px 10px gray;
            box-shadow: 2px 1px 10px gray;
    width: 355px;
    height: 535px;
    text-align: center;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
    margin: 25px;
    background: transparent;
    border-radius: 40px;
}

.Image>p {
    font-family: Dosis, 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    color: black;
    font-weight: 600;
}

.Image:hover {
    -webkit-transform: scale(1.05);
        -ms-transform: scale(1.05);
            transform: scale(1.05);
}
#cardtxt{
    cursor: pointer;
}

@media screen and (max-width:1200px) {
    .Image {
        width:300px;
        height: 500px;
    }
    .imgImage{
        width:300px;
        height: 365px;
    }
}


