.InputFieldName,
.SignEmail,
.Mobile,
.PasswordOne,
.PasswordTwo {
    width: 90%;
    height: 45px;
    border: 2px solid blueviolet;
    text-align: center;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    margin-top: 12px;
}
.HideSignUpOuterDiv{
    visibility: hidden;
}

.SignUpOuterDiv {
    text-align: center;
    display: block;
}

.dob {
    margin-top: 22px;
    width: 20%;
}

.GenderParagraph {
    font-size: 15px;
    display: -ms-flexbox;
    display: flex;
    width: 300px;
    margin-top: 22px;
    text-align: center;
    margin-left: 37%;
}

.ResetCreateAccount,
.ContinueToCreate {
    width: 90%;
    max-width: 890px;
    height: 45px;
    color: rgb(65, 157, 194);
    border: 2px solid skyblue;
    background: white;
    font-weight: 600;
    text-align: center;
    padding: 5px;
    overflow: hidden;
    margin-top: 12px;
}

@media screen and (max-width: 640px) {
    .GenderParagraph {
        width: 100px;
        display: block;
        margin-left: 38%;
    }
}

@media screen and (max-width: 280px) {
    .GenderParagraph {
        margin-left: 0px;
    }
    .dob {
        width: 100px;
    }
}


