.Select-menu-outer {
    width: 350px;
}

.Select-control {
    width: 350px;
}
.ForBlockDisplay{
    background: whitesmoke;
    position: relative;
    
}
.hideListOfDoctorDiv{
    display: none;
}
.showListOfDoctorDiv{
    display: block
}
.wid {
    display: -ms-flexbox;
    display: flex;
    -webkit-box-shadow: 1px 1px 1px 2px darkgray;
            box-shadow: 1px 1px 1px 2px darkgray;
    background: rgba(0, 0, 0, 0.7);
    width: 100%;
    text-align: center;
    height: 125px;
    
}

.ChooseFilters {
    text-align: center;
    color: whitesmoke;
    width: 250px;
    font-weight: 600;
    font-size: 24px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    height: 25px;
    margin-top: 10px;
    position: relative;
    top: 20px;
    left: 2%;
    min-width: 150px;
}

.Location,
.Fee,
.Speciality {
    position: relative;
    left: 50px;
    background: rgb(179, 226, 231);
    width: 350px;
    height: 85px;
    font-weight: 600;
    min-width: 150px;
    margin-top: 15px;
    margin-left: 15px;
    color: black;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.ApplyFilterButton{
    width: 250px;
    background: white;
    border: 1px solid rgba(7, 182, 7, 0.7);
    font-family:  Dosis, 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-size: 20px;
    border-radius: 8px;
    color: brown;
    font-weight: 600;
    height: 45px;
    position: relative;
    top: 15px;
    left: 50px;
    margin-right: 15px;
    margin-top: 19px;
    min-width: 120px;
    margin-left: 15px;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
}
.ApplyFilterButton:hover{
    background: rgba(7, 182, 7, 0.7);
    color: white;
    -webkit-transform: scale(1.02);
        -ms-transform: scale(1.02);
            transform: scale(1.02);
}
.DoctorProfileDiv{
    width: 100%;
    display: -ms-inline-flexbox;
    display: inline-flex;
    overflow: scroll;

}
@media screen and (max-width:1500px){
    .wid{ 
        overflow-x: scroll;
        overflow-y: hidden;
        height: 346px;
    }
    .Location,
    .Fee,
    .Speciality {
        z-index: 1;
        
    }
    .ApplyFilterButton{
        width: 200px;
        height: 55px;
    }
    .Location,.Speciality,.Fee{
        top: 6%;
    }
    .ChooseFilters,.ApplyFilterButton{
        top: 10%;
    }
}   


