.Contact{
    position: relative;
    top: -15px;
    height: 600px;
    font-family: 'josefin sans',cursive;
}/*
.InnerContactDiv{
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: ;
}*/
.AddressDiv{
    position: absolute;
    left: 10%;
    top: 70px;
    bottom: 50px;
    background: rgba(255, 255, 255, 0.6);
    width: 400px;
    text-align: center;
    height: 430px;
    font-size: 34px;
    border-radius: 30px;
    border: 2px solid black;
}
.MessageForm>h2{
    font-size: 35px;
    font-family: 'Indie Flower', cursive;
    font-family: 'Poor Story', cursive;
}
.Sharing{
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-preferred-size: auto;
        flex-basis: auto;
}
.MessageForm{
    position: absolute;
    border-radius: 30px;
    border: 2px solid black;
    width: 400px;
    background: rgba(255, 255, 255, 0.6);
    right: 10%;
    height: 500px;
    display: block;
    top: 40px;
    text-align: center;
    font-size: 25px;
}
.MainMessage{
    text-align: center;
    height: 150px;
    width: 350px;
    border-radius: 10px;
    font-size: 25px;
    margin: 5px;
    font-family: 'Indie Flower', cursive;
    font-family: 'Poor Story', cursive;
    border: 2px solid black;
    background: transparent;
}
.InputClassSendMessage{
    background: transparent;
    text-align: center;
    width: 350px;
    font-size: 25px;
    height: 35px;
    margin: 5px;
    border-radius: 10px;
    font-family: 'Indie Flower', cursive;
    font-family: 'Poor Story', cursive;
    border: 2px solid black;
}
.Reset,.Send{
    width: 130px;
    border-radius: 7px;
    margin: 9px;
    height: 35px;
    font-size: 25px;
    font-family: 'Indie Flower', cursive;
    font-family: 'Poor Story', cursive;
    border: 2px solid black;
    position: relative;
    bottom: 25px;
    background: transparent;
}
.Send{
    position: absolute;
    right: 10px;  
    font-size: 25px;
    font-family: 'Indie Flower', cursive;
    font-family: 'Poor Story', cursive;
}
.Reset{
    font-size: 25px;
    position: absolute;
    left: 10px;   
    font-family: 'Indie Flower', cursive;
    font-family: 'Poor Story', cursive;
}
.InputClassSendMessage:focus,
.MainMessage:focus,.Reset:focus,.Send:focus{
    background: black;
    color: white;
}
@media screen and (max-width: 1200px){
    .MessageForm{
        position: absolute;
        top: 600px;
        left: 10%;
        width: 300px;
    }
    .Contact{
        height: 1160px;
    }
    .AddressDiv{
        width: 300px;
        height: 520px;
    }
    .Sharing{
        bottom: 15px;
    }
    .InputClassSendMessage{
        background: transparent;
        text-align: center;
        width: 280px;
        font-size: 25px;
        height: 35px;
        margin: 5px;
        border-radius: 10px;
        font-family: 'Indie Flower', cursive;
        font-family: 'Poor Story', cursive;
        border: 2px solid black;
    }
    .MainMessage{
        text-align: center;
        height: 150px;
        width: 280px;
        border-radius: 10px;
        font-size: 25px;
        margin: 5px;
        font-family: 'Indie Flower', cursive;
        font-family: 'Poor Story', cursive;
        border: 2px solid black;
        background: transparent;
    }
}



