
.DoctorPhoto{
    text-align: center;
    top: 0px;
    height: 150px;
    width: 300px;
    -webkit-box-shadow: 2px 3px 2px gray;
            box-shadow: 2px 3px 2px gray;
}
.DoctorINFO{
    text-align: center;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    padding:2px 10px;
    margin-bottom: 5px;
   /*padding-left: 5px;*/
}
.DoctorPhotoRound{
    width: 200px;
    height: 150px;
    border-radius: 50%;
    border: 2px solid rgb(156, 146, 146);
}
.BookAppointmentButton{
    width: 100%;
    border: 2px solid rgb(10, 198, 204);
    height: 35px;
    color: white;
    background: rgb(10, 198, 204);
    font-weight: 800;
    font-family: Dosis,cursive;
    font-size: 18px;
    position: absolute;
    margin-top: 0px;
    left: 0px;
}

.DoctorID{
    width: 300px;
    height: 590px;
    text-align: center;
    -webkit-box-shadow: 2px 3px 5px gray;
            box-shadow: 2px 3px 5px gray;
    margin: 10px;
    position: relative;
    background: white;
    border-radius: 10px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}
.DocInfoTitle{
    color:brown;
    margin-bottom: 2px;
    font-size: 18px;
}
.InfoParaClass{
    margin: 5px;
    font-size: 15px;
}



