.InputFieldForgotPass{
    color: black;
    padding: 8px;
    width: 88%;
    text-align: center;
    max-width: 800px;
    height: 35px;
    margin-top: 12px;
    border: 2px solid blueviolet;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;  
}
.SubmitKeyButton{
    width: 93%;
    max-width: 820px;
    height: 45px;
    color: rgb(14, 90, 119);
    border: 2px solid skyblue;
    font-weight: 600;
    text-align: center;
    padding: 5px;
    overflow: hidden;
    margin-top: 12px;
    background: white;
}
.SubmitKeyButton:hover{
    background: skyblue;
    color: white;
}
.EmailCheckText{
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 20px;
}


